.parallax > use {
  &:nth-child(1) {
    animation: move-forever 12s linear infinite;
    animation-delay: -2s; }
  &:nth-child(2) {
    animation: move-forever-other 12s linear infinite;
    animation-delay: -2s;
    animation-duration: 5s; } }


@keyframes move-forever {
    0% {
        transform: translate(-90px , 0%); }
    100% {
        transform: translate(85px , 0%); } }
@keyframes move-forever-other {
    0% {
        transform: translate(90px , 0%); }
    100% {
        transform: translate(-85px , 0%); } }

