@mixin borrad($radius) {
	-webkit-border-radius: $radius;
	-moz-border-radius:    $radius;
	-ms-border-radius:     $radius;
	border-radius:         $radius; }

@mixin block-center {
	position: absolute;
	left: 50%;
	top: 50%;
	-moz-transform: translate(-50%, -50%);
	-ms-transform: translate(-50%, -50%);
	-webkit-transform: translate(-50%, -50%);
	-o-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%); }

$colorBlack05: rgba(0, 0, 0, 0.5);
$colorBlack06: rgba(0, 0, 0, 0.6);
$colorBlack07: rgba(0, 0, 0, 0.7);

$colorWhite03: rgba(255, 255, 255, 0.3);
$colorWhite05: rgba(255, 255, 255, 0.5);
$colorWhite07: rgba(255, 255, 255, 0.7);

$colorGreen: #52ac62;

@mixin FFOSR {
	font-family: 'Open Sans' {
    font-weight: normal;
    font-style: normal; } }

@mixin FFOSB {
	font-family: 'Open Sans' {
    font-weight: bold;
    font-style: normal; } }

@mixin FFOSI {
	font-family: 'Open Sans' {
    font-weight: bold;
    font-style: italic; } }

@mixin FFUB {
	font-family: 'Ubuntu' {
    font-weight: bold;
    font-style: normal; } }
